.tech-tag {
  box-sizing: border-box;
  margin: 0 8px 0 0;
  color: #000000d9;
  font-size: 14px;
  font-variant: tabular-nums;
  line-height: 1.5715;
  list-style: none;
  font-feature-settings: "tnum";
  display: inline-block;
  height: auto;
  padding: 0 7px;
  font-size: 12px;
  line-height: 20px;
  white-space: nowrap;
  background: #fafafa;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  opacity: 1;
  transition: all .3s;
  color: #6e86ff;
  background: #f0f5ff;
  border-color: #adc6ff;
}

.project-row .item-wrap {
  /* width: 33%; */
  flex: 0 0 28%;
}

.project-row .columns {
  display: flex;
}

.project-row .des-wrap {
  margin-left: 3rem;
}

.img-wrap {
  height: 138px;
  overflow: hidden;
}

.thanks {
  color: white;
  font-weight: bold;
  font-size: 4rem;
  text-align: center;
  line-height: 17rem;
}

@media only screen and (max-width: 767px) {
  .project-row .item-wrap {
    width: 56%;
    margin: 0 auto 10px;
  }
  
  .project-row .columns {
    flex-direction: column;
  }
  
  .project-row .des-wrap {
    margin-left: 0;
  }

  .project-row .des-wrap p {
    margin: 0;
  }

  .tech-wrap {
    text-align: center;
  }

  #portfolio-wrapper .columns {
    margin-bottom: 60px;
  }

  .img-wrap {
    height: 92px;
  }

  .thanks {
    font-size: 3rem;
    line-height: 15rem;
  }

  #portfolio h1 {
    /* font: 15px/24px 'opensans-semibold', sans-serif;
    text-transform: uppercase;
    letter-spacing: 1px; */
    text-align: center;
    padding-left: 0;
    /* margin-bottom: 48px; */
